import React, { useState } from 'react';
import './InitialOverlayDialog.css';
const InitialOverlayDialog = () => {
    const [showDialog, setShowDialog] = useState(true);
    const [hideDialogForever, setHideDialogForever] = useState(false);

    const handleConfirm = () => {
        setShowDialog(false);
        if (hideDialogForever) {
            localStorage.setItem('hideInitialDialog', 'true');
        }
    };

    const handleClose = () => {
        setShowDialog(false);
    };

    if (!showDialog) {
        return null;
    }

    return (
        <div className="initial-overlay-dialog">
            <div className="dialog-content">
                <h2>Primeros pasos!</h2>
                <p>Para disear una formacin para tu bolo, dirgete al men principal y selecciona la opcin "Percusionistas". En la ventana emergente, elige a las personas que deseas aadir o eliminar de tu diseo. Las personas seleccionadas se aadirn al lienzo y podrs moverlas libremente arrastrndolas.</p>
                <p>Si deseas eliminar a una persona directamente desde el lienzo, realiza un doble clic sobre la persona. Aparecer un men en el que debes seleccionar la opcin de "Eliminar".</p>
                <p>Si deseas cambiar el instrumento de una persona, sigue los mismos pasos anteriores y en el men emergente selecciona "Cambiar instrumento". Aparecer una ventana emergente donde podrs elegir el instrumento que deseas asignarle a esa persona.</p>
                <p>Por ltimo, si deseas exportar el diseo como una imagen, ve al men principal y selecciona la opcin "Exportar imagen".</p>

                <div className="bottom-controls">
                    <button className="confirm-button" onClick={handleConfirm}>Entendido</button>
                    <label className="checkbox-label">
                        <input
                            type="checkbox"
                            className="hide-dialog-checkbox"
                            checked={hideDialogForever}
                            onChange={() => setHideDialogForever(!hideDialogForever)}
                        />
                        No volver a mostrar
                    </label>
                </div>
            </div>
        </div>
    );
};

export default InitialOverlayDialog;
